// Bookmark teaser styling
.ama_book_mark {
  border-bottom: solid 1px $gray-20;
  @include breakpoint($bp-small) {
    display: flex;
    flex-direction: row-reverse;
    padding-top: 14px;
  }
  .ama_bookmark_left {
    border-bottom: none;
    padding-bottom: 96px;
    padding-top: 0;
    @include breakpoint($bp-small) {
      padding-bottom: 56px;
    }
  }
  .ama_bookmark_right {
    padding: $gutter/2 0;
    @include breakpoint($bp-small) {
      padding-bottom: 56px;
      &.ama__search-result__image {
        display: flex;
        flex-direction: column;
        &> div { 
          flex: auto;
          align-self: flex-start;
        }
        .flag {
          flex: auto;
          align-self: flex-end;
        }
      }
      .flag {
        a::before {
          left: 35px;
        }
        &.action-unflag {
          a {
            pointer-events: all;
          }
          a::before {
            background-image: url('../images/RemoveIcon_Locker.svg');
          }
          a:hover {
            &::before {
              background-image: url('../images/RemoveHoverIcon_Locker.svg');
            }
          }
          // When re-flagging unflagged bookmarks
          &:not(.flag-glossary) {
            a::before {
              left: 20px;
            }
          }
        }
      }
    }
    &:not(.ama__search-result__image) {
      display: flex;
      @include breakpoint($bp-small) {
        padding: 28px 0 56px;
      }
      .flag {
        align-self: flex-end;
      }
    }
  }
  &.ama__search-result:hover {
    .ama__search-result__title {
      text-decoration: none;
    }
  }
  .ama__search-result__text:hover {
    .ama__search-result__title {
      text-decoration: underline;
    }
  }
  .flag {
    margin-top: 0;
    @include breakpoint($bp-small max-width) {
      position: absolute;
      bottom: 35px;
      right: 0;
    }
  }
}

// Flag/bookmark styling
.flag {
  text-align: center;
  border: 1px solid #a28ab5;
  margin-top: 35px;
  @include breakpoint($bp-small max-width) {
    width: 87px;
    a {
      font-size: 14px;
    }
  }
  @include breakpoint($bp-small) {
    min-width: 180px;
    max-width: 180px;
    max-height: 46px;
  }
  a {
    color: #310f4a;
    text-decoration: none;
    position: relative;
    @include breakpoint($bp-small) {
      padding: 9px 0 8px;
      display: block;
    }
    &::before {
      display: block;
      width: 19px;
      height: 19px;
      margin: 4px auto -8px;
      content: "";
      background-repeat: no-repeat;
      background-position: 0 0;
      background-size: cover;
      @include breakpoint($bp-small) {
        padding-left: 5px;
        width: 21px;
        height: 26px;
        margin: 0 auto;
        position: absolute;
        left: 20px;
        top: 10px;
      }
    }
  }
  &.action-flag {
    @include breakpoint($bp-small) {
      a {
        padding-left: 27px;
      }
    }
    a::before {
      background-image: url('../images/BookmarksMenuIcon_Locker.svg');
    }
    a:hover {
      &::before {
        background-image: url('../images/BookmarkedIcon_Locker.svg');
      }
    }
  }
  &.action-unflag {
    a {
      pointer-events: none;
      @include breakpoint($bp-small) {
        padding-left: 16px;
      }
      &::before {
        background-image: url('../images/BookmarkedIcon_Locker.svg');
      }
      &:hover {
        &::before {
          background-image: url('../images/BookmarksMenuIcon_Locker.svg');
        }
      }
    }
  }
  .ajax-progress,
  .ajax-progress-throbber {
    display: none;
  }
  p.js-flag-message {
    display: none;
  }
}

// My bookmark view styling
.view-my-bookmark {
  .views-exposed-form {
    margin-top: 28px;
    .form-actions {
      margin-top: 7px;
    }
  }
  .views-infinite-scroll-content-wrapper {
    .views-row:first-child {
      .ama_book_mark {
        border-top: 1px solid #a28ab5;
      }
    }
    .views-row:last-child {
      .ama_book_mark {
        border-bottom: none;
      }
    }
  }
  li.pager__item {
    text-align: center;
  }
}

// Empty bookmark styling
.no-bookmarks {
  border-top: 2px solid $gray-20;
  border-bottom: 1px solid $gray-20;
  padding: 14px 0;
}

// Layout overrides
.ama__basic {
  .flag {
    margin-bottom: 27px;
  }
}

.hub-page {
  .flag {
    margin-bottom: 14px;
  }
}

.app_description {
  .flag {
    margin-bottom: 34px;
  }
}

//  My Topics view styling.
.topic-search {
  margin-top: 28px !important;
  border-bottom: 1px solid #CBCBCB;
}
.my-topic-listing {
  flex-direction: row;
  .text {
    padding-right: 14px;
    h2 {
      a {
        text-decoration: none;
      }
    }
  }
  .flag {
    margin-top: 0;
    &.action-unflag {
      a {
        pointer-events: all;
      }
      a::before {
        background-image: url('../images/FollowHoverIcon_Locker.svg');
        @include breakpoint($bp-small) {
          left: 25px;
        }
      }
    }
  }
  .flag-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    justify-items: flex-end;
    align-items: flex-end;
    padding-bottom: 18px;
    @include breakpoint($bp-med) {
      align-items: right;
    }
  }
}

.index-page {
  .flag.action-unflag {
    a {
      pointer-events: none;
    }
  }
}

.flag-topic {
  &.action-flag {
    a::before {
      background-image: url('../images/FollowIcon_Locker@1x.svg');
      @include breakpoint($bp-small) {
        left: 25px;
      }
    }
    a:hover {
      text-decoration: underline;
      &::before {
        background-image: url('../images/FollowHoverIcon_Locker.svg');
      }
    }
  }
  &.action-unflag {
    a::before {
      background-image: url('../images/FollowingIcon_Locker.svg');
      @include breakpoint($bp-small) {
        left: 25px;
      }
    }
  }
}
